import React, { useState } from 'react';
import { Lock, User, KeyRound, ArrowRight, CheckCircle, XCircle } from 'lucide-react';

// --- TYPE DEFINITIONS ---
interface LoginData {
  usernameOrEmail: string;
  password: string;
  rememberMe: boolean;
}

// --- PLACEHOLDER COMPONENTS ---

// Component mô phỏng danh sách sản phẩm (sẽ hiện ra sau khi đăng nhập thành công)
const ProductListing: React.FC = () => {
  return (
    <div className="min-h-screen bg-gray-100 flex flex-col items-center justify-center p-8">
      <CheckCircle className="w-16 h-16 text-green-500 mb-4" />
      <h1 className="text-4xl font-bold text-gray-800">Đăng Nhập Thành Công!</h1>
      <p className="text-xl text-gray-600 mt-2">Chào mừng đến với danh sách sản phẩm.</p>
    </div>
  );
};

// --- LOGIN FORM COMPONENT (Slightly modified from user's original) ---
interface LoginFormProps {
  onSubmit: (data: LoginData) => void;
  isLoading: boolean;
}

const LoginForm: React.FC<LoginFormProps> = ({ onSubmit, isLoading }) => {
  const [usernameOrEmail, setUsernameOrEmail] = useState('');
  const [password, setPassword] = useState('');
  const [rememberMe, setRememberMe] = useState(false);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (!isLoading) {
      onSubmit({ usernameOrEmail, password, rememberMe });
    }
  };

  return (
    <div className="flex justify-center items-center min-h-screen bg-gray-50 p-4">
      <div className="bg-white p-8 md:p-12 rounded-xl shadow-2xl w-full max-w-sm transform transition-all duration-300 hover:shadow-xl">
        {/* Phần Header */}
        <div className="flex items-center mb-6">
          <div className="p-3 mr-4 rounded-xl bg-white border-2 border-red-100 shadow-md">
            <Lock className="w-7 h-7 text-red-600" strokeWidth={2} />
          </div>
          <div>
            <h2 className="text-3xl font-bold text-gray-800">Login Here</h2>
          </div>
        </div>

        {/* Thông báo mô tả */}
        <p className="text-sm text-gray-500 mb-8">
          Dữ liệu cá nhân của bạn sẽ được sử dụng để quản lý tài khoản và hỗ trợ trải nghiệm trên website.
        </p>

        <form onSubmit={handleSubmit} className="space-y-6">
          {/* Input: Username / email address */}
          <div>
            <div className="flex items-center border-b border-gray-300 py-3 focus-within:border-red-600 transition-colors duration-200">
              <User className="w-5 h-5 text-gray-500 mr-3" strokeWidth={1.5} />
              <input
                type="text"
                placeholder="Username / email address"
                value={usernameOrEmail}
                onChange={(e) => setUsernameOrEmail(e.target.value)}
                className="w-full text-lg appearance-none bg-transparent border-none focus:outline-none placeholder-gray-500"
                required
                disabled={isLoading}
              />
            </div>
          </div>

          {/* Input: Password */}
          <div>
            <div className="flex items-center border-b border-gray-300 py-3 focus-within:border-red-600 transition-colors duration-200">
              <KeyRound className="w-5 h-5 text-gray-500 mr-3" strokeWidth={1.5} />
              <input
                type="password"
                placeholder="Password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                className="w-full text-lg appearance-none bg-transparent border-none focus:outline-none placeholder-gray-500"
                required
                disabled={isLoading}
              />
            </div>
          </div>

          {/* Remember me & Forget Password */}
          <div className="flex justify-between items-center pt-2">
            <label className="flex items-center cursor-pointer">
              <input
                type="checkbox"
                checked={rememberMe}
                onChange={(e) => setRememberMe(e.target.checked)}
                className="form-checkbox h-4 w-4 text-red-600 rounded-sm border-gray-300 focus:ring-red-500"
                disabled={isLoading}
              />
              <span className="ml-2 text-sm text-gray-700">Remember me</span>
            </label>
            <a href="#" className="text-sm text-gray-500 hover:text-red-600 font-medium">
              Forget Password
            </a>
          </div>

          {/* Button: Login Now */}
          <button
            type="submit"
            className={`w-full flex justify-center items-center font-semibold py-3 px-4 rounded-lg shadow-md transition duration-200 mt-8 ${
              isLoading
                ? 'bg-red-400 cursor-not-allowed'
                : 'bg-red-600 hover:bg-red-700 text-white'
            }`}
            disabled={isLoading}
          >
            {isLoading ? (
              <span className="flex items-center text-white">
                <svg className="animate-spin -ml-1 mr-3 h-5 w-5 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                  <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                  <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                </svg>
                Đang Đăng Nhập...
              </span>
            ) : (
              <span className="flex items-center text-lg text-white">
                Login Now
                <ArrowRight className="w-5 h-5 ml-2" strokeWidth={2} />
              </span>
            )}
          </button>
        </form>
      </div>
    </div>
  );
};


// --- MAIN APP COMPONENT ---
export default function App() {
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [loginStatus, setLoginStatus] = useState<'idle' | 'loading' | 'error'>('idle');
  const [errorMessage, setErrorMessage] = useState<string | null>(null);

  // Đây là nơi chứa LOGIC ĐĂNG NHẬP THỰC TẾ
  const handleLogin = async (data: LoginData) => {
    setLoginStatus('loading');
    setErrorMessage(null);

    // Dữ liệu đăng nhập (chỉ hiển thị)
    console.log("Dữ liệu đăng nhập:", data);

    /**
     * ⚠️ THAY THẾ PHẦN NÀY BẰNG CÁC GỌI API THẬT CỦA BẠN ⚠️
     * ------------------------------------------------------------------
     * Ví dụ sử dụng fetch/axios:
     * * try {
     * const response = await fetch('/api/login', {
     * method: 'POST',
     * headers: { 'Content-Type': 'application/json' },
     * body: JSON.stringify({
     * username: data.usernameOrEmail,
     * password: data.password
     * })
     * });
     *
     * if (response.ok) {
     * // Đăng nhập thành công
     * const result = await response.json();
     * localStorage.setItem('authToken', result.token); // Lưu token
     * setIsLoggedIn(true);
     * setLoginStatus('idle');
     * } else {
     * // Đăng nhập thất bại (Ví dụ: 401 Unauthorized)
     * const errorData = await response.json();
     * setErrorMessage(errorData.message || 'Sai tên đăng nhập hoặc mật khẩu.');
     * setLoginStatus('error');
     * }
     * } catch (error) {
     * setErrorMessage('Lỗi kết nối mạng hoặc server.');
     * setLoginStatus('error');
     * }
     */

    // --- MÔ PHỎNG: Dùng setTimeout để giả lập độ trễ API ---
    await new Promise(resolve => setTimeout(resolve, 1500));

    // GIẢ ĐỊNH TÊN ĐĂNG NHẬP ĐÚNG: 'test' VÀ MẬT KHẨU ĐÚNG: '123456'
    if (data.usernameOrEmail === 'test' && data.password === '123456') {
      setIsLoggedIn(true);
      setLoginStatus('idle');
    } else {
      setErrorMessage('Sai tên đăng nhập hoặc mật khẩu. Vui lòng thử lại.');
      setLoginStatus('error');
    }
  };
  // ------------------------------------------------------------------

  if (isLoggedIn) {
    return <ProductListing />;
  }

  return (
    <div className="relative">
      {/* Hiển thị thông báo lỗi (nếu có) */}
      {loginStatus === 'error' && errorMessage && (
        <div className="absolute top-4 left-1/2 transform -translate-x-1/2 w-full max-w-md p-4 bg-red-100 border border-red-400 text-red-700 rounded-lg shadow-lg flex items-center z-50 animate-fadeIn">
          <XCircle className="w-5 h-5 mr-3 flex-shrink-0" />
          <p className="text-sm font-medium">{errorMessage}</p>
          <button onClick={() => setErrorMessage(null)} className="ml-auto text-red-700 hover:text-red-900">
            &times;
          </button>
        </div>
      )}

      <LoginForm onSubmit={handleLogin} isLoading={loginStatus === 'loading'} />
    </div>
  );
}